<template>
	<div class="ToDoBox">  
	    <yd-navbar slot="navbar" title="工作流程">
	    	<a href="javascript:;" @click="goIndex" slot="left">
	        	<yd-navbar-back-icon></yd-navbar-back-icon>
	    	</a>
	    </yd-navbar>
		<move :tab="true" :listData="moveList">
			<div slot = "page1" class="pageBox">
	  			<to-do-list></to-do-list>
	    	</div>
	    	<div slot = "page2" class="pageBox">
	  			<attend-list></attend-list>
	    	</div>
	    	<div slot = "page3" class="pageBox">
	  			<apply-list tabKey="all"></apply-list>
	    	</div>
	    	<div slot = "page4" class="pageBox">
	  			<copy-list></copy-list>
	    	</div>
		</move>
	</div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue';
import {loadRefreshList} from '@/assets/app.js';
import {ListTheme,  ListOther} from 'vue-ydui';
import ToDoList from '@/components/bpm/MyProcess/ToDoList';
import AttendList from '@/components/bpm/MyProcess/AttendList';
import ApplyList from '@/components/bpm/MyProcess/ApplyList';
import CopyList from '@/components/bpm/MyProcess/copyList';
import Move from '@/components/Move';
import router from '@/router/index';

export default {
	data(){
  		return {
      		list:[],
		  	moveList:{
		  		title:{
					1:'我的待办',
					2:'我的已办',
					3:'我的申请',
					4:'我的抄送'
				}
		  	}
    	}
  	},
	components:{
		ToDoList,
		AttendList,
		CopyList,
		ApplyList,
		Move
	},
    methods: {
  		goIndex(){
  			this.$router.push({name:"index"})
  		}
    }
  
}
</script>

<style type="text/css" scoped>
	.ToDoBox{
		display: flex;
		flex-direction: column;
		height: 100%;
	}
	
	.ToDoBox > .customBox{
		
	}
	
	.vux-tab-item{
		transition: .3s color;
	}
	
	section{
		height: calc(100% - 1rem);
		display: flex;
		flex-direction: column;
	}
	
	section .vux-tab{
		height: .88rem;
	}
	
	section .vux-slider{
		height: calc(100% - .88rem);
	}

	section >>> ._v-content{
		position: relative;
	}
	
	.ToDoBox >>> .nav h1{
		text-align: center;
	}
	
  
  
</style>


