
import Vue from 'vue';
import {loadRefreshList} from '@/assets/app.js';
import {ListTheme,  ListOther} from 'vue-ydui';
import listItem from '@/components/common/ListItem';
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin);
Vue.component(listItem.name, listItem);

export default {
name: 'to-do-list',
data(){
return {
list:[],
page:0,
total:0,
showTips:false,
index: 0
}
},
components:{

},
created() {

this.loadList();
},
methods:{
loadList(){
var url=_baseUrl +"/mobile/bpm/myTasks.do";
var params="pageIndex="+this.page+"&pageSize=10";
var self=this;
loadRefreshList(url,params,this,data=>{
this.total = data.total;
},true);
},
infinite(data) {

},
preLoadType(){
var url=_baseUrl +"/mobile/bpm/myTasks.do";
},
getStatusHmtl(status){
var str="";
switch(status){
case "RUNNING":
str="<span class='runing'>运行中</span>";
break;
case "SUCCESS_END":
str="<span class='finish'>结束</span>";
break;
case "ABORT_END":
str="<span class='abort'>异终止</span>";
break;
case "DISCARD_END":
str="<span class='discard'>作废</span>";
break;
}
return str;
},
getFirstLetter(word,status){
if(!word) return "";
var str = word.substr(0,1).toUpperCase(),
textColor = '';

switch(status){
case "RUNNING":
textColor = "<span class='runing-bg'>"+ str +"</span>";
break;
case "SUCCESS_END":
textColor = "<span class='finish-bg'>"+ str +"</span>";
break;
case "ABORT_END":
textColor = "<span class='abort-bg'>"+ str +"</span>";
break;
case "DISCARD_END":
textColor = "<span class='discard-bg'>"+ str +"</span>";
break;
}
return textColor;
}
