<template>
	<div class="to-do-list">
		<yd-pullrefresh :callback="loadList" ref="pullrefreshList_">
		<yd-list theme="4" v-if="list && list.length>0">
	        <rx-list-item 
	        	v-for="item in list" 
	        	:key="item.id" 
	        	:href="{name:'getInstInfo',params:{instId:item.actInstId,type:'act'}}" 
	        	type='link'
	        	class="item-a"
	    	>
	    		<span slot="list-title" class="list-title">{{getTitle(item.subject)}}</span>
				<div slot="content" class="item-info">
					<div class="message-icon" v-html="getFirstLetter(item.subject)"></div>
					<ul>
						<li>
							<span>{{item.createTime}}</span>
						</li>
						<li>
							<span class="list-price">{{item.subject}}</span>
						</li>
					</ul>
					<i class="yd-icon-right"></i>
				</div>
	        	<div class="item-content">
					<div slot="title" class="item-title">
		          		<span>当前步骤</span>
		          		<span>{{item.nodeName}}</span>
		        	</div>
					<yd-list-other slot="other">
						<span class="list-price">进展状态</span>
						<span class='runing'>送达</span>
					</yd-list-other>
	        	</div>
	        </rx-list-item>
	    </yd-list>
	    </yd-pullrefresh>
	    <rx-nodata :amount="list.length"></rx-nodata>
	</div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue'
import {loadRefreshList} from '@/assets/app.js';
import {ListTheme,  ListOther} from 'vue-ydui';
import listItem from '@/components/common/ListItem';
//import router from '@/router/index';
import NoData from '@/components/common/NoData';
Vue.component(listItem.name, listItem);
Vue.component(NoData.name, NoData);


export default {
	name: 'attend-list',
	data(){
    	return {
      		list:[],
			page:0,
			total:0,
			merage:false
    	}
	},
	created() {
  		this.loadList();
	},
	methods:{
   		loadList(){
	    	var url=_baseUrl +"/mobile/bpm/copyToMe.do";
			var params="pageIndex="+this.page+"&pageSize=10";
			var self=this;
			loadRefreshList(url,params,this,data=>{
				
			},this.merage);
	    },
	    loadPage(){
	    	this.merage=true;
	    	this.loadList();
	    },
	    getTitle( title ){
	    	var indexTl =  title.search('-由');
	    	return title.slice( 0 , indexTl );
	    },
		getFirstLetter(word){
			console.log(word)
	    	if(!word) return "";
			var str = word.substr(0,1).toUpperCase();
	  		return '<span class=runing-bg>'+ str +'</span>';
		}
	}
}


</script>

<style type="text/css" scoped>
	.to-do-list{
		height: 100%;
		position: relative;
		overflow-y:auto ;
		overflow-x: hidden;
		-webkit-overflow-scrolling:touch;
	}
	
	.tips{
		position: absolute;
		top: 0rem;
		left: 50%;
		width: 2rem;
		text-align: center;
		margin-left: -1rem;
		z-index: 1;
		background: rgba(0,0,0,.5);
		padding: .1rem 0;
		border-radius: 1rem;
		transition: all .8s ease-in-out;
		opacity: 0;
	}
  
  	.tips span{
		font-size: .28rem;
		color: #fff;
	}
  
 	.show-tips{
 		opacity: 1;
 		transform: translate( 0, .4rem);
 	}
 	
 	.hide-tips{
 		transform: translate(0, 0rem);
 	}
 	
 	.yd-list-theme4{
 		background: transparent;
 		margin: .24rem 0;
 	}
 	
 	.item-a{
 		border-bottom: none;
 		margin:0 auto .24rem auto;
 		width: calc(100% - .48rem);
 		padding: .12rem;
 		background: #fff;
 		border-radius: .2rem;
 		box-shadow: 0px 4px 16px 0px rgba(179, 179, 179, 0.2);
 	}	
 	 	
	.yd-list-theme4 >>> .yd-list-mes{
	  	display: flex;
		flex-direction: column;
		justify-content: center;
	}
  
	.yd-list-theme4 >>> .yd-list-title{
		font-weight: normal;
		border-bottom: 1px solid #ececec;
		padding-bottom: .12rem;
	}
  
	.list-title{
		font-size: .34rem;
		color: #666;
		text-align: center;
		padding: .06rem 0;
	}
  
	.message-img>span{
		color: #fff;
		border: none;
		line-height: .86rem;
	}
  
	.item-info{
		display: flex;
	}
  
	.item-info>ul{
		width: calc(100% - 1.32rem);
		display: flex;
		flex-direction: column;
	}
  
	.item-info>ul::after{
		content: '';
		display: block;
		clear: both;
	}
  
	.item-info>ul>li{
	  	width: 100%;
	  	display: flex;
	  	align-items: center;
	  	margin-right: .12rem;
	  	margin-top: .12rem;
	}
  
	.item-info>ul>li:nth-of-type(1){
		
	}
  
	.item-info>ul>li:nth-of-type(1) span{
	  	font-size: .22rem;
	  	color: #b2b2b2;
	  	line-height: .22rem;
	}
  
	.item-info>ul>li:nth-of-type(2){
	  	
	}
  
	.item-info>ul>li:nth-of-type(2) span{
	  	font-size: .28rem;
	  	white-space: nowrap;
	  	text-overflow: ellipsis;
	  	overflow: hidden;
	  	line-height: .28rem;
	}
  
	.item-info .yd-icon-right:before{
  		font-size: .4rem;
  		line-height: .8rem;
	}
  
	.item-content{
  		display: flex;
  		width: 100%;
  		padding-top: .12rem;
	}
  
	.item-content .item-title{
  		flex-direction: column;
  		width: 50%;
  		border-right: 1px solid #ececec;
  		padding: .12rem 0;
	}
  
	.item-content .item-title span,
	.item-content .yd-list-other span{
  		width: 100%;
  		text-align: center;
	}
  
	.item-content .item-title span:nth-of-type(1),
	.item-content .yd-list-other span:nth-of-type(1){
  		font-size: .26rem;
  		line-height: .26rem;
  		color: #b3b3b3;
	}
  
	.item-content .item-title span:nth-of-type(2),
	.item-content .yd-list-other span:nth-of-type(2){
  		font-size: .32rem;
  		line-height: .32rem;
  		margin-top: .12rem;
	}
	
	.item-content .item-title span:nth-of-type(2){
  		color: #0daaf6;
	}
  
	.item-content .yd-list-other{
 	 	display: flex;
  		flex-direction: column;
  		width: 50%;
  		padding: 0;
  		padding: .12rem 0;
	}
  
	.icon-my-tolist:before{
    	color:#5fe463 !important;
	}

	.icon-already-flow:before{
    	color:#fac256 !important;;
	}
  
</style>


