import AttendList from '@/components/bpm/MyProcess/AttendList';
import ApplyList from '@/components/bpm/MyProcess/ApplyList';
import CopyList from '@/components/bpm/MyProcess/copyList';
import Move from '@/components/Move';
import router from '@/router/index';

export default {
	data(){
  		return {
      		list:[],
		  	moveList:{
		  		title:{
					1:'我的待办',
					2:'我的已办',
