import Vue from 'vue'
import {loadRefreshList} from '@/assets/app.js';
import {ListTheme,  ListOther} from 'vue-ydui';
import listItem from '@/components/common/ListItem';
//import router from '@/router/index';
import NoData from '@/components/common/NoData';
Vue.component(listItem.name, listItem);
Vue.component(NoData.name, NoData);


export default {
	name: 'attend-list',
	data(){
    	return {
      		list:[],
			page:0,
			total:0,
			merage:false
    	}
	},
	created() {
  		this.loadList();
	},
	methods:{
   		loadList(){
	    	var url=_baseUrl +"/mobile/bpm/copyToMe.do";
			var params="pageIndex="+this.page+"&pageSize=10";
			var self=this;
			loadRefreshList(url,params,this,data=>{
				
			},this.merage);
	    },
	    loadPage(){
	    	this.merage=true;
	    	this.loadList();
	    },
	    getTitle( title ){
	    	var indexTl =  title.search('-由');
	    	return title.slice( 0 , indexTl );
	    },
		getFirstLetter(word){
			console.log(word)
	    	if(!word) return "";
			var str = word.substr(0,1).toUpperCase();
	  		return '<span class=runing-bg>'+ str +'</span>';
		}
	}
}





