<template>
	<div class="to-do-list">
		<yd-pullrefresh 
			:callback="loadList" 
			ref="pullrefreshList_"
		>
			<yd-list theme="4" v-if="list && list.length>0">
		        <rx-list-item 
		        	v-for="item in list" 
		        	:key="item.id" 
		        	:href="{name:'task',params:{taskId:item.id}}" 
		        	type='link'
		        	class="item-a"
		    	>
		    		<span slot="list-title" class="list-title">{{item.bpmTypeName}}</span>
						<div slot="content" class="item-info">
							<div class="message-icon" v-html="getFirstLetter(item.bpmTypeName,item.status)"></div>
							<ul>
								<li>
									<span>{{item.createTime}}</span>
								</li>
								<li>
									<span class="list-price">{{item.description}}</span>
								</li>
							</ul>
							<i class="yd-icon-right"></i>
						</div>
		        	<div class="item-content">
						<div slot="title" class="item-title">
			          		<span>当前步骤</span>
			          		<span>{{item.name}}</span>
			        	</div>
						<yd-list-other slot="other">
							<span class="list-price">进展状态</span>
							<span v-html="getStatusHmtl(item.status)"></span>
						</yd-list-other>
		        	</div>
		        </rx-list-item>
	      	</yd-list>
		<!--</scroller>-->
		</yd-pullrefresh>
	</div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue';
import {loadRefreshList} from '@/assets/app.js';
import {ListTheme,  ListOther} from 'vue-ydui';
import listItem from '@/components/common/ListItem';
import  { ToastPlugin } from 'vux';
Vue.use(ToastPlugin);
Vue.component(listItem.name, listItem);

export default {
	name: 'to-do-list',
	data(){
	  	return {
	    	list:[],
			page:0,
			total:0,
			showTips:false,
			index: 0
		}
  	},
  	components:{
  		
  	},
	created() {
		
    	this.loadList();
	},
	methods:{
    	loadList(){
    		var url=_baseUrl +"/mobile/bpm/myTasks.do";
			var params="pageIndex="+this.page+"&pageSize=10";
			var self=this;
			loadRefreshList(url,params,this,data=>{
				this.total = data.total;
			},true);
	    },
		infinite(data) {

		},
		preLoadType(){
			var url=_baseUrl +"/mobile/bpm/myTasks.do";
		},
		getStatusHmtl(status){
	  		var str="";
	  		switch(status){
	  			case "RUNNING":
	  				str="<span class='runing'>运行中</span>";
	  				break;
	  			case "SUCCESS_END":
	  				str="<span class='finish'>结束</span>";
	  				break;
	  			case "ABORT_END":
	  				str="<span class='abort'>异终止</span>";
	  				break;
	  			case "DISCARD_END":
	  				str="<span class='discard'>作废</span>";
	  				break;
	  		}
	  		return str;
		},
		getFirstLetter(word,status){
	    	if(!word) return "";
			var str = word.substr(0,1).toUpperCase(),
				textColor = '';
				
	  		switch(status){
	  			case "RUNNING":
	  				textColor = "<span class='runing-bg'>"+ str +"</span>";
	  				break;
	  			case "SUCCESS_END":
	  				textColor = "<span class='finish-bg'>"+ str +"</span>";
	  				break;
	  			case "ABORT_END":
	  				textColor = "<span class='abort-bg'>"+ str +"</span>";
	  				break;
	  			case "DISCARD_END":
	  				textColor = "<span class='discard-bg'>"+ str +"</span>";
	  				break;
	  		}
	  		return textColor;
		}
		/*,
		refresh(data) {
		    setTimeout(() => {
		    var url=_baseUrl +"/mobile/bpm/myTasks.do";
				var params="pageIndex="+this.page+"&pageSize=10";
				var self=this;
				loadRefreshList(url,params,this,data=>{
					for(let i = 0;i<data.rowList.length;i++){
						data.rowList[i].overTime = Math.random();
					}
					this.list = data.rowList;
					this.total = data.total;
				});
					data();
					this.showTips = true;
					setTimeout(()=>{
						this.showTips = false;
					},2000)
			}, 1000);
		}*/
	}
}
</script>

<style type="text/css" scoped>
	.to-do-list{
		height: 100%;
		position: relative;
		overflow-y:auto ;
		overflow-x: hidden;
		-webkit-overflow-scrolling:touch;
	}
	
	.tips{
		position: absolute;
		top: 0rem;
		left: 50%;
		width: 2rem;
		text-align: center;
		margin-left: -1rem;
		z-index: 1;
		background: rgba(0,0,0,.5);
		padding: .1rem 0;
		border-radius: 1rem;
		transition: all .8s ease-in-out;
		opacity: 0;
	}
  
  	.tips span{
		font-size: .28rem;
		color: #fff;
	}
  
 	.show-tips{
 		opacity: 1;
 		transform: translate( 0, .4rem);
 	}
 	
 	.hide-tips{
 		transform: translate(0, 0rem);
 	}
 	
 	.yd-list-theme4{
 		background: transparent;
 		margin: .24rem 0;
 	}
 	
 	.item-a{
 		border-bottom: none;
 		margin:0 auto .24rem auto;
 		width: calc(100% - .48rem);
 		padding: .12rem;
 		background: #fff;
 		border-radius: .2rem;
 		box-shadow: 0px 4px 16px 0px rgba(179, 179, 179, 0.2);
 	}	
 	 	
	.yd-list-theme4 >>> .yd-list-mes{
	  	display: flex;
		flex-direction: column;
		justify-content: center;
	}
  
	.yd-list-theme4 >>> .yd-list-title{
		font-weight: normal;
		border-bottom: 1px solid #ececec;
		padding-bottom: .12rem;
	}
  
	.list-title{
		font-size: .34rem;
		color: #666;
		text-align: center;
		padding: .06rem 0;
	}
  
	.message-img>span{
		color: #fff;
		border: none;
		line-height: .86rem;
	}
  
	.item-info{
		display: flex;
	}
  
	.item-info>ul{
		width: calc(100% - 1.32rem);
		display: flex;
		flex-direction: column;
	}
  
	.item-info>ul::after{
		content: '';
		display: block;
		clear: both;
	}
  
	.item-info>ul>li{
	  	width: 100%;
	  	display: flex;
	  	align-items: center;
	  	margin-right: .12rem;
	  	
	}
  
	.item-info>ul>li:nth-of-type(1){
		margin-top: .12rem;
	}
  
	.item-info>ul>li:nth-of-type(1) span{
	  	font-size: .22rem;
	  	color: #b2b2b2;
	  	line-height: .22rem;
	}
  
	.item-info>ul>li:nth-of-type(2){
		margin-top: .06rem;
	}
  
	.item-info>ul>li:nth-of-type(2) span{
	  	font-size: .28rem;
	  	line-height: .3rem;
	  	white-space: nowrap;
	  	text-overflow: ellipsis;
	  	overflow: hidden;
	  	padding: .06rem 0;
	}
  
	.item-info .yd-icon-right:before{
  		font-size: .4rem;
  		line-height: .8rem;
	}
  
	.item-content{
  		display: flex;
  		width: 100%;
  		padding-top: .12rem;
	}
  
	.item-content .item-title{
  		flex-direction: column;
  		width: 50%;
  		border-right: 1px solid #ececec;
  		padding: .12rem 0;
	}
  
	.item-content .item-title span,
	.item-content .yd-list-other span{
  		width: 100%;
  		text-align: center;
	}
  
	.item-content .item-title span:nth-of-type(1),
	.item-content .yd-list-other span:nth-of-type(1){
  		font-size: .26rem;
  		line-height: .28rem;
  		color: #b3b3b3;
  		padding: .06rem 0;
	}
  
	.item-content .item-title span:nth-of-type(2),
	.item-content .yd-list-other span:nth-of-type(2){
  		font-size: .32rem;
  		line-height: .32rem;
  		margin-top: .12rem;
	}
	
	.item-content .item-title span:nth-of-type(2){
  		color: #0daaf6;
	}
  
	.item-content .yd-list-other{
 	 	display: flex;
  		flex-direction: column;
  		width: 50%;
  		padding: 0;
  		padding: .12rem 0;
	}
  
	.icon-my-tolist:before{
    	color:#5fe463 !important;
	}

	.icon-already-flow:before{
    	color:#fac256 !important;;
	}
  
</style>

<!--
	作者：1130483391@qq.com
	时间：2017-12-25
	描述：
	
	
	
	
	
<template>
  <yd-layout>
    <yd-navbar slot="navbar" title="我的待办">
      <a href="javascript:;" @click="goBack" slot="left">
        <yd-navbar-back-icon></yd-navbar-back-icon>
      </a>
    </yd-navbar>
    <yd-pullrefresh :callback="loadList" ref="pullrefreshList_">
    	<yd-list theme="4" v-if="list && list.length>0">
        <rx-list-item v-for="item in list" :key="item.id" :href="{name:'task',params:{taskId:item.id}}" type='link'>
          <div class="message-img">
								<span>{{getFirstLetter(item.description)}}</span>
					</div>
        	<div class="item-content">
        	  	<div slot="title" class="item-title">
	          		<span>{{item.description}}</span>
	          	</div>
					    <yd-list-other slot="other">
					    	<div>
					          <span class="list-price" ><em>当前步骤:</em>{{item.name}}</span>
					           <span class="list-price">{{item.createTime}}</span>
					      </div>
					    </yd-list-other>
        	</div>
          
          
        </rx-list-item>
      </yd-list>
     
    </yd-pullrefresh>
    
    <rx-nodata :amount="list.length"></rx-nodata>

    <yd-tabbar slot="tabbar">
    	<yd-tabbar-item title="首页" :link="{name:'index'}" >
        <yd-icon name="index" slot="icon"></yd-icon>
      </yd-tabbar-item>
      <yd-tabbar-item title="我的待办" link="#" active>
        <yd-icon name="my-tolist" slot="icon"></yd-icon>
        <yd-badge slot="badge" type="danger" v-if="total>0">{{displayCount(total)}}</yd-badge>
      </yd-tabbar-item>
      <yd-tabbar-item title="我的已办" :link="{name:'myAttend'}">
        <yd-icon name="already-flow" slot="icon"></yd-icon>
      </yd-tabbar-item>
    </yd-tabbar>

  </yd-layout>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue';
import {loadRefreshList} from '@/assets/app.js';
import {ListTheme,  ListOther} from 'vue-ydui';
import listItem from '@/components/common/ListItem';
import NoData from '@/components/common/NoData';

Vue.component(listItem.name, listItem);
Vue.component(NoData.name, NoData);



export default {
  data(){
    return {
      list:[],
		  page:0,
		  total:0
    }
  },
  created() {
       this.loadList();
  },
  methods:{
    loadList(){
       var url=_baseUrl +"/mobile/bpm/myTasks.do";
				var params="pageIndex="+this.page+"&pageSize=10";
				var self=this;
				loadRefreshList(url,params,this,data=>{
					//_self.tabItems[0].badge=data.total;
					this.total=data.total;
				},true);
    },
    getFirstLetter(word){
			return word.substr(0,1).toUpperCase();
	}
  }
}
</script>

<style type="text/css" scoped>
  #scrollView{
    overflow-x: auto !important;
  }

  .icon-my-tolist:before{
    color:#5fe463 !important;
  }

  .icon-already-flow:before{
    color:#fac256 !important;;
  }
</style>
-->



